@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap";
@import "https://fonts.googleapis.com/css2?family=Outfit:wght@500&display=swap";
@import "https://fonts.googleapis.com/css2?family=Lato&display=swap";
@import "https://fonts.googleapis.com/css2?family=League+Spartan&display=swap";
body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: League Spartan, sans-serif;
}

.full-page {
  width: 100vw;
  height: 100vh;
}

a {
  text-decoration: none;
}

.jumbotron {
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  font-family: League Spartan, sans-serif;
  animation: 1s ease-out fadeInDown;
  display: flex;
}

.jumbo-title {
  text-transform: capitalize;
  color: #fff;
  background-image: linear-gradient(-225deg, #231557 0%, #44107a 29%, #ff1361 67%, #ff9100 100%);
  background-size: 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0;
  font-size: 40px;
  animation: 10s reverse textclip;
  display: inline-block;
}

@keyframes textclip {
  to {
    background-position: 200%;
  }
}

.footer {
  justify-content: center;
  width: 100%;
  display: flex;
}

.social-links {
  margin: 0;
  padding: 0;
  list-style: none;
}

.social-icon {
  vertical-align: middle;
  width: 40px;
  height: 40px;
  margin-right: 5px;
}

.social-link {
  align-items: center;
  margin-right: 50px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.circular-image {
  object-fit: cover;
  border: 3px solid #ccc;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-top: -50px;
  margin-left: 30px;
  margin-right: 30px;
}

.projects {
  justify-content: center;
  width: 100%;
  padding: 50px 0;
  display: flex;
}

.bio {
  justify-content: center;
  width: 100%;
  height: 100%;
}

.topImage {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 30vh;
  display: inline-block;
}

.tl {
  width: 100%;
  height: 10vh;
  margin-top: 10px;
}

.jumbotron-content {
  justify-content: space-between;
  padding: 0 30px;
  display: flex;
}

@media (width <= 768px) {
  .circular-image {
    width: 128px;
    height: 128px;
  }

  .jumbotron-content {
    flex-direction: column;
    align-items: center;
    padding: 0 30px;
    display: flex;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.aboutMe {
  max-width: 800px;
  margin: 0;
  padding-top: 50px;
}

.aboutDescription {
  color: gray;
  font-size: large;
}

