@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

@import url("https://fonts.googleapis.com/css2?family=League+Spartan&display=swap");

body,
html {
  font-family: "League Spartan", sans-serif;
  height: 100%;
  width: 100%;
  margin: 0;
}

.full-page{
  height: 100vh;
  width: 100vw;
}

a{
  text-decoration: none;
}

.jumbotron {
  animation: fadeInDown 1s ease-out; 
  font-family: "League Spartan", sans-serif;

  align-items: center;
  display: flex;
  
  flex-direction: column;

  width: 100%;
  height: 100%;
}

.jumbo-title {
  text-transform: capitalize;
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #ff9100 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  margin-bottom: 0px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 10s reverse;
  display: inline-block;
  font-size: 40px;
  /* font-weight: bold; */
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

.footer {
  /* padding: 50px 0 50px 0; */

  width: 100%;
  
  display: flex;
  justify-content: center;
}


.social-links{
  list-style: none;
  margin: 0px;
padding: 0px;

}

.social-icon {
  margin-right: 5px;
  vertical-align: middle;
  height: 40px;
  width: 40px;
}

.social-link {
  display: flex;
  text-decoration: none;
  margin-right: 50px;
  font-weight: bold;
  display: inline-block;
  align-items: center;
}


.circular-image {
  margin-right: 30px;
  margin-left: 30px;
  margin-top: -50px;
  border: 3px solid #ccc;
  width: 200px; /* Adjust the width and height as per your requirements */
  height: 200px;
  border-radius: 50%;
  object-fit: cover; /* Preserve the aspect ratio of the image */
}

.projects {
  padding: 50px 0 50px 0;

  width: 100%;

  display: flex;
  justify-content: center;
}

.bio {
  height: 100%;
  width: 100%;
  justify-content: center;
}

.topImage {
  object-fit: cover;
  width: 100%;
  display: inline-block;
  height: 30vh;
  
  object-position: center;
}

.tl {
  margin-top: 10px;
  height: 10vh;
  width: 100%;
  /* border-radius: 100%/50px 9px 90px 9px; */
  /* background-image: linear-gradient(
    -225deg,
    #b3a7df 0%,
    #a089b9 29%,
    #db8ba6 67%,
    #ddc3a0 100%
  ); */
}

.jumbotron-content {
  
  justify-content: space-between;
  padding: 0 30px 0 30px;


  display: flex;
  
}

@media (max-width: 768px) {
  /* Apply styles only for screens with a maximum width of 768px (adjust as needed) */
  .circular-image {
    width: 128px; /* Adjust the size for smaller screens */
    height: 128px;
  }


  .jumbotron-content {
  
    
    padding: 0 30px 0 30px;
  
  
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
}


@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

 .aboutMe{
max-width: 800px;
padding-top: 50px;
margin: 0;

}
.aboutDescription{
  font-size: large;
  color: gray;
}

